onst userCount = ref(0);\nconst nonLicensedCount = ref(0);\n\nonBeforeMount(() => {\n    getOrganizationGroupDetails();\n})\nfunction getOrganizationGroupDetails() {\n    executor.execute(async () => {\n        allOrgCount.value = organizationGroup.value.organizations.length;\n        licensedOrgCount.value = organizationGroup.value.organizations.filter(org => org.accountType == \"premium\").length\n        userCount.value = organizationGroup.value.users!.length;\n        const allApps = organizationGroupUseCase.getAllAppsForOrgGroup(organizationGroup.value);\n        allAppCount.value = allApps.length;\n        licensedAppCount.value = allApps.filter(app => app.licensed).length;\n        nonLicensedCount.value = allOrgCount.value - licensedOrgCount.value;\n    }, { loading: true });\n}\nfunction CreateApp(templateName?: string) {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/application/addOrUpdateApp.vue`)),\n        { closable: true, title: \"Create Application\", height: \"383px\", width: \"466px\" },\n        { templateName, organizationGroupId: routeParams.organizationGroupId }\n    );\n}\n</script>\n<template>\n    <div style=\"margin-top: 32px;\">\n        <div class=\"container-fluid\">\n            <div class=\"row\">\n                <div class=\"col\">\n                    <div class=\"card \">\n                        <div class=\"card-header\">\n                            <span class=\"card-title\">Organizations</span>\n                            <i class=\"mdi mdi-account-group-outline\"></i>\n                        </div>\n                        <div class=\"card-body\">\n                            <div class=\"info\">\n                                <span class=\"info-number\">{{ allOrgCount }}</span>\n                                <span class=\"info-text\">Total</span>\n                            </div>\n                            <div class=\"info\">\n                                <span class=\"info-number\">{{ licensedOrgCount }}</span>\n                                <span class=\"info-text\">Licensed</span>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"col\">\n                    <div class=\"card \">\n                        <div class=\"card-header\">\n                            <span class=\"card-title\">Applications</span>\n                            <img src=\"../../../../images/total-app-icon.svg\" alt=\"\">\n                        </div>\n                        <div class=\"card-body\">\n                            <div class=\"info\">\n                                <span class=\"info-number\">{{ allAppCount }}</span>\n                                <span class=\"info-text\">Total</span>\n                            </div>\n                            <div class=\"info\">\n                                <span class=\"info-number\">{{ licensedAppCount }}</span>\n                                <span class=\"info-text\">Licensed</span>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"col\">\n                    <div class=\"card \">\n                        <div class=\"card-header\">\n                            <span class=\"card-title\">Users</span>\n                            <i class=\"mdi mdi-account-outline\"></i>\n                        </div>\n                        <div class=\"card-body\">\n                            <div class=\"info\">\n                                <span class=\"info-number\">{{ userCount }}</span>\n                                <span class=\"info-text\">Total</span>\n                            </div>\n                            <div class=\"info\">\n                                <span class=\"info-number\">{{ userCount }}</span>\n                                <span class=\"info-text\">Licensed</span>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n        <p class=\"app-text\">Create New Application</p>\n        <div v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length > 0\"\n            class=\"createapp-container\">\n            <div class=\"text\"></div>\n            <div class=\"blankapp-logo-card\" @click=\"CreateApp()\">\n                <img src=\"../../../../images/blank-app.svg\" alt=\"\" class=\"blankapp-logo\" />\n            </div>\n            <p class=\"title\">Blank Application</p>\n            <div class=\"blankapp-logo-card\" style=\"left: 211px\" @click=\"CreateApp('newAppTemplate1')\">\n                <img src=\"../../../../images/template-app.svg\" alt=\"\" class=\"blankapp-logo\" />\n            </div>\n            <p class=\"title\" style=\"left: 226px\">Template Application</p>\n        </div>\n        <OrgApplications :apps=\"orgApps\" :hideOrgName=\"true\"></OrgApplications>\n    </div>\n</template>\n<style scoped>\n.card-header img {\n    width: 24px;\n    height: 36px;\n}\n\n.card-header i {\n    font-size: 24px;\n}\n\n.info {\n    display: grid;\n    justify-items: center;\n}\n\n.info-text {\n    color: var(--Text-color, #212121);\n    font-family: Roboto;\n    font-size: 16px;\n    font-style: normal;\n    font-weight: 700;\n    line-height: normal;\n    text-transform: capitalize;\n}\n\n.info-number {\n    color: var(--Accent, #2C5A86);\n    font-family: Roboto;\n    font-size: 32px;\n    font-style: normal;\n    font-weight: 700;\n    line-height: normal;\n    text-transform: capitalize;\n}\n\n.card-title {\n    color: var(--Plateau-Navy, #151233);\n    font-family: Roboto;\n    font-size: 20px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: normal;\n    text-transform: capitalize;\n    margin-bottom: 0;\n}\n\n.card-header {\n    display: flex;\n    padding: 12px 20px;\n    justify-content: space-between;\n    align-items: center;\n    background-color: transparent;\n}\n\n.card-body {\n    display: flex;\n    align-items: center;\n    justify-content: space-around;\n    overflow: hidden;\n    padding: 32px 0;\n    border-radius: 4px;\n    box-shadow: 1px 1px 2px 0px rgba(34, 17, 34, 0.16);\n    background: var(--cvcv, #FFF);\n    gap: 32px;\n}\n\n.navigator {\n    display: flex;\n    justify-content: stretch;\n}\n\n.home-link {\n    color: var(--Plateau-Navy, #151233);\n    font-family: Roboto;\n    font-size: 15px;\n    font-style: normal;\n    font-weight: 400;\n    line-height: normal;\n    text-decoration-line: underline;\n    cursor: pointer;\n}\n\n.card {\n    border-radius: 4px;\n    background: var(--cvcv, #FFF);\n    box-shadow: 1px 1px 2px 0px rgba(34, 17, 34, 0.16);\n}\n\n.breadcrumb-div {\n    display: flex;\n    align-items: center;\n    background: #fff;\n    border-bottom: 1px solid #D5D5D5;\n    padding: 5px 24px;\n}\n\n.app-text {\n    font-size: 18px;\n    color: #151233;\n    font-weight: 500;\n    margin-left: 7px;\n    margin-top: 32px;\n}\n\n.createapp-container {\n    position: relative;\n    background: #ffffff 0% 0% no-repeat padding-box;\n    border-radius: 10px;\n    width: 100%;\n    height: 187px;\n    min-width: 425px;\n    margin-top: 12px;\n}\n\n.title {\n    position: absolute;\n    top: 147px;\n    left: 48px;\n    color: #151233;\n    font-size: 15px;\n}\n\n.text {\n    position: absolute;\n    left: 24px;\n    top: 24px;\n}\n\n.card-filters {\n    cursor: pointer;\n    margin-top: -12px;\n}\n\n.blankapp-logo {\n    position: absolute;\n    top: calc((100% - 57px) / 2);\n    left: calc((100% - 57px) / 2);\n}\n\n.blankapp-logo-card {\n    width: 163px;\n    height: 119px;\n    border-radius: 4px !important;\n    position: absolute;\n    background: #eeeeee 0% 0% no-repeat padding-box;\n    border: 1px solid #d5d5d5;\n    top: 20px;\n    left: 24px;\n    cursor: pointer;\n}\n</style>"],"sourceRoot":""}])